import { AbstractControl } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

/**
 * Get params from all router-outlet scopes
 */
export function getRouteParams(route: ActivatedRoute) {
  let params = route.snapshot.params;
  params = { ...route.snapshot.queryParams, ...params };
  if (route.children) {
    for (const r of route.children) {
      params = { ...getRouteParams(r), ...params };
    }
  }
  return params;
}

/** Amount value should be positive value **/
export function nonZeroValidator(control: AbstractControl): { [key: string]: any } | null {
  if (!control.value || Number(control.value) <= 0) {
    return { nonZero: true };
  } else {
    // eslint-disable-next-line no-null/no-null
    return null;
  }
}

export function generateId() {
  return Math.floor(Math.random() * Date.now()).toString();
}
